import {CartoLayer,CartoSQLLayer, MAP_TYPES, setDefaultCredentials,BASEMAP} from '@deck.gl/carto';
import React, {useState, useCallback,useEffect} from 'react';
import {render} from 'react-dom';
import {StaticMap} from 'react-map-gl';
import DeckGL from '@deck.gl/react';
import { LEGEND_COLORS } from './barChart';



const INITIAL_VIEW_STATE = {
    latitude: -33.878197,
    longitude: 151.148648,
    zoom:10
  };

setDefaultCredentials({
username: 'gsc-admin',
apiKey: 'default_public'//'fFbU97TNhKdhIO4yYyFjdA'
});

const MAPBOX_ACCESS_TOKEN = 'pk.eyJ1IjoiYnNuYXlhbiIsImEiOiJja3Ewczd3MW4wN3Z3MnVvNDNsenlzNnFsIn0.f8nk8yrxIeCKOlzIy1b-Pg'

export const POLYGON_COLORS ={
    COLOR_YES: [0,0,0,0],
    COLOR_NO :[248,15,54],
    COLOR_GREEN :[16,196,118]
  }
  
const AccessMap = ()=> {


   
     
  
    const layerAccess = new CartoLayer({
      id:'access-uni',
      type: MAP_TYPES.QUERY,
      data: 'select the_geom_webmercator, tobreak from serviceareapolygons_rings_universities',
    
      getFillColor: object =>[...LEGEND_COLORS[object.properties.tobreak],210],
      getLineColor: object =>LEGEND_COLORS[object.properties.tobreak],
      lineWidthMinPixels: .10,
      pickable: false,
      
    });

    const layerUniversity = new CartoLayer({
      id:'university',
      type: MAP_TYPES.QUERY,
      data: 'select the_geom_webmercator, university_campus_name as uni_name from universities',
    
      getFillColor: [0,0,0],
      getLineColor: [255,255,255],
      lineWidthMinPixels: 1,
      pointRadiusMinPixels:6,
      pickable: true,
      
    });

    const layerDistrict = new CartoLayer({
      id:'district',
      type: MAP_TYPES.QUERY,
      data: `select the_geom_webmercator, district from "gsc-admin".districtboundary `,
    
      getFillColor: [0,0,0,0],
      getLineColor: [35, 35, 35],
      lineWidthMinPixels: 1.2,
      
      pickable: false,
      
    });

    
  
    const getTooltip = ({object}) => {
      if (!object) return false;
      const {district,uni_name} = object.properties;
      
  
      return {
        html: `<div>University: ${uni_name}</div>`,
        style:{
          
          fontSize: '0.8em'
         
        }
      }
    };

  
    return (
      <div>
       
        <DeckGL
          width="100%"
          height="100%"
          initialViewState={INITIAL_VIEW_STATE}
          getTooltip = {getTooltip}
          controller={true}
          layers={[layerAccess,layerDistrict,layerUniversity]}
        >
          <StaticMap
            reuseMaps
            mapStyle= {BASEMAP.PRISTON}//"mapbox://styles/mapbox/dark-v9"
            mapboxApiAccessToken= {MAPBOX_ACCESS_TOKEN}
            preventStyleDiffing
          />
        </DeckGL>
      </div>
    );
  }


  export default AccessMap;